.card {
  width: 80%;
  padding: 30px 90px 90px 90px;
  border: 6px solid rgba(182, 182, 182, 0.7);
  box-shadow: 20px 20px 0 rgba(0, 55, 255, 0.8);
  border-radius: 50px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 300px;
  margin: 10px 0 0 50px;
}

.card h2 {
  color: rgba(0, 0, 0, 0.3);
  font-size: 60px;
  text-transform: uppercase;
}

.card .row {
  position: relative;
  width: 100%;
  display: grid;
  grid: auto / auto auto;
  grid-template-columns: repeat(auto-fit, minmax(40%, 1fr));
  grid-gap: 30px;
}

.card .row .col {
  position: relative;
  width: 100%;
  margin: 30px 20px 40px 0;
  transition: 0.5s;
}
.card .row .form-group-message {
  position: relative;
  width: 100%;
  height: 40px;
  color: #ffffff;
}
.card .row .form-group {
  position: relative;
  width: 100%;
  height: 40px;
  color: #ffffff;
}
.card .row .form-group-message input,
.card .row .form-group-message textarea{
  position: absolute;
  width: 90%;
  height: 100%;
  background: transparent;
  outline: none;
  font-size: 24px;
  padding: 10px 0 10px 20px;
  border: 5px solid rgba(0, 0, 0, 0.3);
  box-shadow: 10px 10px 0 rgba(0, 0, 0, 0.3);
  color: #000cf8;
  border-radius: 50px;
  margin: 50px 0 0 -150px;
}
.card .row .form-group-message input:focus,
.card .row .form-group-message textarea:focus {
  border: 5px solid #0f3fed;
  transition: all 0.5s;
}
.card .row .form-group input,
.card .row .form-group textarea {
  position: absolute;
  width: 90%;
  height: 100%;
  background: transparent;
  outline: none;
  font-size: 24px;
  padding: 10px 0 10px 20px;
  border: 5px solid rgba(0, 0, 0, 0.3);
  box-shadow: 10px 10px 0 rgba(0, 0, 0, 0.3);
  color: #000cf8;
  border-radius: 50px;
  margin: 50px 0 0 -150px;
}
