}

.card .row input[type="submit"] {
  border: 5px solid rgba(72, 113, 189, 0.7);
  box-shadow: 10px 10px 0 rgba(47, 80, 141, 0.7);
  padding: 10px;
  height: 100%;
  width: 50%;
  cursor: pointer;
  outline: none;
  background: transparent;
  text-transform: uppercase;
  color: #5d6cad;
  line-height: 40px;
  font-size: 24px;
  font-weight: 700;
  border-radius: 45px;
  transition: all 0.4s;
}

.card .row input[type="submit"]:hover {
  border: 5px solid rgb(9, 28, 234);
  color: #1001ed;
  transition: all 0.4s;
}
